import axios from 'axios'

const collectData = function (payload) {
    if (!this.isDexLoaded) {
        const tier = (payload || this.$route.params.tier);
        axios.get(`/pokemon/${tier}`).then(res => {
            this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon[0]);
            this.$store.commit("dex/SET_BATTLEDEX", res.data.pokemon);
        });
    }
}

const searchPokemon = function () {
    const word = this.search.toLowerCase();
    if (word === "") {
        this.searched = false
        return;
    }

    let s = [];
    for (let pkmn in this.battledex) {
        if (word.length > this.battledex[pkmn].slug.length) continue;
        else if (this.battledex[pkmn].slug.startsWith(word)) {
            s.push(this.battledex[pkmn]);
        }
    }
    this.searched = s
}

const changeTier = function (e) {
    const tier = e.target.value;
    console.log(tier)
    this.searched = false
    this.$store.commit("dex/SET_BATTLEDEX", false);
    this.collectData(tier)
    this.$router.push(`/dex/${e.target.value}`)
}

export default {
    changeTier,
    collectData,
    searchPokemon
}