<template>
  <section v-if='!isDexLoaded' class='explore__items'>
    <div class='lds-ellipsis'>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </section>

  <section class='list' v-else>
    <div class='navbar'>
      <input
        @keyup='searchPokemon'
        v-model='search'
        class='nav-search'
        type='text'
        placeholder='Search...'
      />
      <select @change='changeTier' :value='tierParam'>
        <option v-for='(tier, key) in tiers' :key='key' :value='tier.path'>{{tier.path}}</option>
      </select>
    </div>

    <router-link
      v-for='(pkmn, key) in (searched || battledex)'
      :key='key'
      :class='`pkmn pokemon-${pkmn.types[0]}`'
      :to='`/dex/${$route.params.tier}/${pkmn.slug}`'
    >
      <img :src='`/2d/${pkmn.slug}.png`' />
    </router-link>
  </section>
</template>

<script>
import EventBus from "@/event.bus";
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      searched: false,
      search: "",
      tiers: [
        { tier: "Over Used", alias: "OU", path: "ou" },
        { tier: "Under Used", alias: "UU", path: "uu" },
        { tier: "Battle Stadium Singles", alias: "BSS", path: "bss" },
        { tier: "Ubers", alias: "UBERS", path: "ubers" },
        { tier: "Video Game Championship", alias: "VGC", path: "vgc" },
        { tier: "Rarely Used", alias: "RU", path: "ru" },
        { tier: "Monotype", alias: "Monotype", path: "monotype" },
        { tier: "Little Cup", alias: "LC", path: "lc" },
        { tier: "Not Fully Evolved", alias: "NFE", path: "nfe" },
      ],
    };
  },
  components,
  computed,
  methods,
  watchers,
  created() {
    this.collectData();
    EventBus.$on("SEARCHED", (payload) => {
      this.searched = payload;
    });

    EventBus.$on("SEARCH-RESET", () => {
      this.searched = false;
    });
  },
};
</script>

<style lang="scss" scoped>
@import "./style";
</style>